import { Suspense } from "react";
import "./App.css";
import Nav from "./components/ui-components/Nav";
import NavHome from "./components/ui-components/NavHome";
import MartianSwapPage from "./pages/MartianSwapPage";
import Team from "./pages/Team";
import QRCodePay from "./components/ui-components/QRCodePay";
import HowToBuy from "./components/ui-components/HowToBuy";
import { VStack, Text, Stack } from "@chakra-ui/react";
import Home from "./pages/Home/";
import Tokenomics from "./pages/Tokenomics";
import {
  BrowserRouter as Router,
  Routes,
  Switch,
  Route,
  Link,
} from "react-router-dom";

import { motion } from "framer-motion";
import Footer from "./components/ui-components/Footer";

export default function App() {
  
  return (
    <Suspense fallback="">
      <NavHome/> 
      

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/Tokenomics" element={<Tokenomics />} />
        <Route path="/MartianSwap" element={<MartianSwapPage />} />
        <Route path="/Team" element={<Team />} />
      </Routes>
     
      <Footer />
    </Suspense>
  );
}
